import React,{Component} from "react";
import {connect} from "react-redux";
import {Modal,Toast,NavBar,Checkbox,Flex,Icon,WhiteSpace,WingBlank,List, Stepper,Button,Carousel} from 'antd-mobile';
const alert = Modal.alert;
import {getDetail} from "../../actions"
import "./index.scss"
import axios from "axios"
axios.defaults.baseURL = "http://39.107.100.105:8000";

import {getBanner} from "./getBanner"
@connect(
    state=>{
            return{...state}
    }
)


export default class Detail extends Component{
 
    componentWillMount(){
        this.props.dispatch(getDetail({mId:this.props.match.params.mId}))
    }
    addShopCar=()=>{
        if(sessionStorage.getItem("username")){
            axios.get("/pc/addShopCar",{params:{mId:this.props.match.params.mId,id:sessionStorage.getItem("userId"),name:this.props.Detail.mName,img:this.props.Detail.mImg,price:this.props.Detail.discountPrice}}).then((res)=>{
                if(res.data.code==200){
                    Toast.success("加入成功",.5,()=>{
                            document.getElementById("addShopCar").className="animated shake";
                        });
                    document.getElementById("addShopCar").className="";
                    
                }else{
                    Toast.fail("加入失败",1);
                }
            })
        }else{
            this.showAlert();
        }
        
    }
    showAlert = () => {
        const alertInstance = alert('提示', '您还未登录', [
          { text: '取消', onPress: () => {}, style: 'default' },
          { text: '确认', onPress: () => this.props.history.push("/login") },
        ]);
        setTimeout(() => {
          console.log('auto close');
          alertInstance.close();
        }, 500000);
    }
    render(){
        console.log(this.props)
        let bannerList=[];
        if(this.props.Detail.mDescription){
           bannerList = getBanner(this.props.Detail.mDescription)
        }
        bannerList.splice(4)
        const Item = List.Item;
        const Brief = Item.Brief;
        return(
            <div>
                {/* <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={()=>this.props.history.go(-1)}
                >详情</NavBar> */}
                <Carousel
                autoplay={false}
                infinite
                >
                {
                    bannerList.map((val,index)=>{
                        return(
                            <div  className="detail-banner" key={index} dangerouslySetInnerHTML={{ __html:val }}/>
                        )
                    })
                }
                </Carousel>
                <List className="my-list">
                <Item wrap className="detail-name">{this.props.Detail.mName}</Item>
                <Item wrap className="detail-intro">{this.props.Detail.slogan}</Item>
                </List>
                <WhiteSpace/>
                <List className="my-list">
                    <Item arrow="horizontal" multipleLine onClick={() => {}}>
                    活动<Brief>【年度盛典】价保双十二，购机12期免息，增双肩包</Brief>
                    </Item>
                    <Item multipleLine>
                    赠品<Brief>基本清洁工具</Brief>
                    </Item>
                    <Item arrow="horizontal" multipleLine onClick={() => {}}>
                    套餐<Brief>官方套餐1</Brief>
                    </Item>
                </List>
                <WhiteSpace/>
                <Item>图文详情</Item>
            <div className="detail-pic" dangerouslySetInnerHTML={{ __html:this.props.Detail.mDescription }}/>
            <div className="footer-btn">
            <Icon onClick={()=>this.props.history.go(-1)} size="lg" type={"left"} />
            <i className="iconfont icon-gouwuche" onClick={()=>{this.props.history.push("/app/shopcar")}}></i>
            <button onClick={this.addShopCar} id="addShopCar">加入购物车</button>
            </div>
            
            </div>
        )
    }
}


