import { Accordion, List,NavBar, Icon, InputItem, WhiteSpace,Button,WingBlank,Toast} from 'antd-mobile';
import { createForm } from 'rc-form';
import React,{Component} from "react";
import "./index.scss";
import axios from "axios"

axios.defaults.baseURL = "http://39.107.100.105:8000";
export default class Login extends Component{
    state = {
        hasError: false,
        registerError:false,
        comfrieError:false,
        value:"",
        pwd:"",
        pwd1:'',
        pwd2:'',
        disabled:true,
        codeTxt:"点击获取验证码",
        isClick:false,

      }
      onErrorClick = () => {
        if (this.state.hasError || this.state.registerError ||this.state.comfrieError) {
          Toast.fail('请检查你的错误');
        }
      }
      onComChange=(pwd2)=>{
          if(pwd2==this.state.pwd1){
            this.setState({
                comfrieError:false,
            })
          }else{
            this.setState({
                comfrieError:true,
            })
          }
          this.setState({
              pwd2
          })
      }
      onResChange=(pwd1)=>{
          if(pwd1.length>=6 && pwd1.length<=12){
                this.setState({
                    registerError:false
                })
          }else{
            this.setState({
                registerError:true
            })
          }
          this.setState({
              pwd1
          })
      }
      onPwdChange=(pwd)=>{
        if(pwd.length>=6 && pwd.length<=12){
              this.setState({
                  registerError:false
              })
        }else{
          this.setState({
              registerError:true
          })
        }
        this.setState({
            pwd
        })
    }
      onChange = (value) => {
        if (value.replace(/\s/g, '').length < 11) {
          this.setState({
            hasError: true,
          });
        } else {
          this.setState({
            hasError: false,
          });
        }
        this.setState({
          value,
        });
      }
      register=()=>{
          
          if(this.state.hasError || this.state.registerError ||this.state.comfrieError || this.state.value==""){
              this.onErrorClick()
          }else{
                axios.post("/user/register",{username:this.state.value,password:this.state.pwd1}).then((res)=>{
                           if(res.data.code==200){
                                Toast.success(res.data.txt,1,()=>{
                                    this.props.history.go(0);
                                })
                           }else{
                            Toast.fail(res.data.txt,1)
                           }
                })
          }
      }
      login=()=>{
        if(this.state.hasError || this.state.registerError ||this.state.comfrieError){
            this.onErrorClick()
        }else{
            axios.post("user/login",{username:this.state.value,password:this.state.pwd}).then((res)=>{
                console.log(res)
                if(res.data.code==200){
                    Toast.success('登录成功',1,()=>{
                        sessionStorage.setItem("username",res.data.userinfo.username);
                        sessionStorage.setItem("userid",res.data.userinfo.userId);
                        this.props.history.go(-1);
                    })
                }else{
                    Toast.fail('账号密码错误')
                }
            })
        }
      }
      getCode=()=>{
            if(this.state.hasError||this.state.value==""){
                Toast.fail("请填写正确手机号")
            }else{
            let code=Math.floor(Math.random()*899999+100000)
            localStorage.setItem("code",code);
            // getCode(this.state.value,code)
            axios.get("/user/getCode",{params:{tel:this.state.value,code}}).then((res)=>{
                // console.log(res)
                Toast.info(res.data.reason,1)
            })
            this.setState({
                disabled:false,
                isClick:true
            })
            let n=10;
            let timer = setInterval(()=>{
                n--
                this.setState({
                    codeTxt:n+"秒后可重新获取",
                })
                if(n==0){
                    clearInterval(timer)
                    this.setState({
                        codeTxt:"点击获取验证码",
                         isClick:false
                    })
                }
            },1000)
        }
      }
      quickLogin=()=>{
          if(this.state.pwd1==localStorage.getItem("code")){
              axios.post("/user/register",{username:this.state.value,password:"000000"}).then((res)=>{
                  if(res.data.code==200){
                      axios.post("/user/login",{username:this.state.value,password:"000000"}).then((res)=>{
                          if(res.data.code==200){
                            sessionStorage.setItem("username",res.data.userinfo.username);
                            sessionStorage.setItem("userid",res.data.userinfo.userId);
                            this.props.history.go(-1)
                          }
                      })
                  }
              })
          }else{
            Toast.fail("验证码错误")
          }
      }
    render(){

        return(
            <div>
                <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => this.props.history.go(-1)}
                >登录/注册</NavBar>
                <div style={{ marginTop: 10, marginBottom: 10 }}>
                    <Accordion accordion openAnimation={{}} className="my-accordion" defaultActiveKey="1" >
                    <Accordion.Panel header="登录" key="1">
                        <div className="login-box">
                        <img src={require("../../../assets/images/icon/logoicon.png")} alt=""/>
                        <InputItem
                        type="phone"
                        placeholder="输入您的手机号"
                        error={this.state.hasError}
                        onErrorClick={this.onErrorClick}
                        onChange={this.onChange}
                        value={this.state.value}
                        >手机号码</InputItem>
                        <InputItem
                        type="password"
                        placeholder="输入您的密码"
                        error={this.state.registerError}
                        onErrorClick={this.onErrorClick}
                        onChange={this.onPwdChange}
                        value={this.state.pwd}
                        >密码</InputItem>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <WingBlank>
                        <Button type="warning" onClick={this.login}>登录</Button>
                        </WingBlank>
                        <WhiteSpace/>

                        </div>
                    </Accordion.Panel>
                    <Accordion.Panel header="手机号快捷登录" className="pad" key="3">
                        <div className="register-box">
                        <img src={require("../../../assets/images/icon/logoicon.png")} alt=""/>
                        <InputItem
                        type="phone"
                        placeholder="输入您的手机号"
                        error={this.state.hasError}
                        onErrorClick={this.onErrorClick}
                        onChange={this.onChange}
                        value={this.state.value}
                        >手机号码</InputItem>
                        <InputItem  ref="getCode" onErrorClick={this.onErrorClick} error={this.state.registerError} value={this.state.pwd1} onChange={this.onResChange} type="password" placeholder="">
                        验证码
                        </InputItem>
                        <List.Item onClick={()=>this.getCode()}>
                        <div
                        style={{ width: '100%', color: '#108ee9', textAlign: 'center' }}
                        >
                        
                        <Button type="ghost" disabled={this.state.isClick}>
                        {this.state.codeTxt}
                        </Button>
                        </div>
                        </List.Item>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <WingBlank>
                        <Button ref="quickLogin" disabled={this.state.disabled} type="warning" onClick={this.quickLogin}>登录</Button>
                        </WingBlank>
                        <WhiteSpace/>  
                        </div>

                    </Accordion.Panel>
                    <Accordion.Panel header="没有账号?点此注册" className="pad" key="2">
                        <div className="register-box">
                        <img src={require("../../../assets/images/icon/logoicon.png")} alt=""/>
                        <InputItem
                        type="phone"
                        placeholder="输入您的手机号"
                        error={this.state.hasError}
                        onErrorClick={this.onErrorClick}
                        onChange={this.onChange}
                        value={this.state.value}
                        >手机号码</InputItem>
                        <InputItem  onErrorClick={this.onErrorClick} error={this.state.registerError} value={this.state.pwd1} onChange={this.onResChange} type="password" placeholder="6-12位数字密码组成">密码</InputItem>
                        <InputItem  onErrorClick={this.onErrorClick} error={this.state.comfrieError} value={this.state.pwd2}  onChange={this.onComChange}type="password" placeholder="确认你的密码">确认密码</InputItem>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <WingBlank>
                        <Button type="warning" onClick={this.register}>注册</Button>
                        </WingBlank>
                        <WhiteSpace/>  
                        </div>

                    </Accordion.Panel>
                    </Accordion>
                </div>
            </div>
        )
    }
}


