import React, {Component} from "react"
import "./index.scss"
// import { Tabs, WhiteSpace,NavBar,Icon, Button } from 'antd-mobile';
import { Popover, NavBar, Icon ,Button,Grid} from 'antd-mobile';
import {connect} from "react-redux";
import axios from "axios";
const Item = Popover.Item;
const myImg = src => <img src={`https://gw.alipayobjects.com/zos/rmsportal/${src}.svg`} className="am-icon am-icon-xs" alt="" />;
const data = Array.from(new Array(9)).map((_val, i) => ({
    icon: 'https://gw.alipayobjects.com/zos/rmsportal/nywPmnTAvTmLusPxHPSu.png',
    text: `
    我的功能${i}`,
  }));
axios.defaults.baseURL = "http://39.107.100.105:8000";
@connect(
    state=>{
            return{...state}
    }
)
export default class Main extends Component{
    state={
        isShow:"none",
        isLogin:"block",
        visible: false,
        selected: '',
    }
    componentWillMount(){
        let username = sessionStorage.getItem("username");
           if(username){
                this.setState({
                    isShow:"block",
                    isLogin:"none"
                })
           }
    }
    handleVisibleChange = (visible) => {
        this.setState({
          visible,
        });
      };

      onSelect=()=>{
        sessionStorage.removeItem("username");
        sessionStorage.removeItem("userId");
        this.props.history.go(0)
      }

    render(){
        let mineShow={
            display:this.state.isShow
        }
        let loginShow={
            display:this.state.isLogin
        }

        return(
            <div>
                <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => this.props.history.go(-1)}
                >个人中心</NavBar>
                <div className="mine-top">
                    <div className="mine-login" style={loginShow}>
                        <p>您还未登录,您可以</p>
                        <Button type="primary" onClick={()=>this.props.history.push("/login")} inline style={{ marginRight: '4px' }}>登录/注册</Button>
                    </div>
                    <div className="mine-info" style={mineShow}>
                        <img src={require("../../../assets/images/mine.png")} alt=""/>
                        <NavBar
                            mode="light"
                            rightContent={
                            <Popover mask
                                overlayClassName="fortest"
                                overlayStyle={{ color: 'currentColor' }}
                                visible={this.state.visible}
                                overlay={[
                                (<Item key="4"  value="scan" icon={(<Icon type='cross-circle' />)} data-seed="logId">退出登录</Item>),
                                ]}
                                align={{
                                overflow: { adjustY: 0, adjustX: 0 },
                                offset: [-10, 0],
                                }}
                                onVisibleChange={this.handleVisibleChange}
                                onSelect={this.onSelect}
                            >
                                <div style={{
                                height: '100%',
                                padding: '0 15px',
                                marginRight: '-15px',
                                display: 'flex',
                                alignItems: 'center',
                                }}
                                >
                                <Icon type="ellipsis" />
                                </div>
                            </Popover>
                            }
                        >
                            {sessionStorage.getItem("username")}
                        </NavBar>
                    </div>
                    <Grid data={data} activeStyle={false} />

                </div>
                 
            </div>
        )
    }
}


