import React, {Component} from "react"
import {NavLink} from "react-router-dom"
import {Modal,Toast,NavBar,Checkbox,Flex,Icon,WhiteSpace,WingBlank,List, Stepper,Button,Result} from 'antd-mobile';
import {connect} from "react-redux";
import {getShopCar} from "../../actions"
import "./index.scss"
const CheckboxItem = Checkbox.CheckboxItem;
const AgreeItem = Checkbox.AgreeItem;
const myImg = src => <img src={src} className="spe am-icon am-icon-md" alt="" />;
const Item = List.Item;
import axios from "axios"
import { Map } from "immutable";
import { clearTimeout } from "timers";
axios.defaults.baseURL = "http://39.107.100.105:8000";
const alert = Modal.alert;
@connect(
    state=>{
            return{...state}
    }
)

export default class ShopCar extends Component{
    state={
        checkOne:[],
        total:0,
        checkAll:false,
        isShow:"block",
        modal2:false,
        modal1:false,
        beforeTotal:0
    }
    showAlert = () => {
        const alertInstance = alert('提示', '您还未登录', [
          { text: '取消', onPress: () => this.props.history.go(-1), style: 'default' },
          { text: '确认', onPress: () => this.props.history.push("/login") },
        ]);
        setTimeout(() => {
          console.log('auto close');
          alertInstance.close();
        }, 500000);
    }
    showAlert2 = (_id,index) => {
        const alertInstance = alert('提示', '确定删除？', [
          { text: '取消', onPress: () => {}, style: 'default' },
          { text: '确认', onPress: () => this.deleteGood(_id,index) },
        ]);
        setTimeout(() => {
          console.log('auto close');
          alertInstance.close();
        }, 500000);
    }
    showModal = key => (e) => {
        e.preventDefault(); // 修复 Android 上点击穿透
        let flag = this.state.checkOne.some((val)=>{
            return val==true
        })
        if(flag){
            this.setState({
                [key]: true,
              });
        }else{
            Toast.info("请选择一个心仪的商品",1)
        }
        
    }
    onClose = key => () => {
        this.setState({
          [key]: false,
        });
      }
    componentWillMount(){
        let username = sessionStorage.getItem("username")
        if(username){
            let id = sessionStorage.getItem("userId")
            this.props.dispatch(getShopCar({id})).then(()=>{
                if(this.props.shopCar.length>0){
                    this.setState({
                        isShow:"none"
                    })
                }
                this.props.shopCar.map((val,index)=>{
                    this.state.checkOne[index]=false
                    this.setState({
                        checkOne:this.state.checkOne
                    })
                }) 
            })
        }else{
            this.showAlert();
        }
     
       
    }

    updateShopCar=(mId,value)=>{
        axios.get("/pc/updateShopCar",{params:{mId,num:value}}).then((res)=>{
            if(res.data.code!=200){
                Toast.fail("未知错误",1)
            }else{
                this.props.dispatch(getShopCar({id:sessionStorage.getItem("userId")})).then(()=>{
                    this.total();
                })
            }
        })

    }
    checkOne=(index)=>{
        this.state.checkOne[index]=!this.state.checkOne[index]
        this.setState({
            checkOne:this.state.checkOne
        })
        let flag = this.state.checkOne.every((val)=>{
            return val==true
        })
        this.setState({
            checkAll:flag
        }) 
        this.total();

    }
    checkAll=()=>{
        this.setState({
            checkAll:!this.state.checkAll
        })
        for(let i=0;i<this.state.checkOne.length;i++){
            this.state.checkOne[i]=!this.state.checkAll;
            this.setState({
                checkOne:this.state.checkOne
            })
            this.total();
        }
    }
    total=()=>{
        this.state.total=0;
        console.log(this.props.shopCar)
        if(this.props.shopCar.length>=1){
            for(let i=0;i<this.state.checkOne.length;i++){
                if(this.state.checkOne[i]){
                    this.state.total+=this.props.shopCar[i].num*1*this.props.shopCar[i].price*1
                }
            }
        }
       
        this.setState({
            total:this.state.total
        })
        console.log(this.state.total)

    }
    deleteGood=(_id,index)=>{
        axios.get("/pc/deleteGood",{params:{_id}}).then((res)=>{
           if(res.data.code==200){
                this.refs["box"+index].className="animated zoomOutDown";
                let timer=setTimeout(()=>{
                    this.props.dispatch(getShopCar({id:sessionStorage.getItem("userId")})).then(()=>{
                        console.log(this.props.shopCar)
                            this.state.checkOne.splice(index,1)
                            this.setState({
                                checkOne:this.state.checkOne
                            })
                             this.total();
                        if(this.props.shopCar.length>=1){
                            this.setState({
                                isShow:"none"
                            })
                        }else{
                            this.setState({
                                isShow:"block"
                            })
                        }
                        this.refs["box"+index].className="";
                    })
               },800)
           }
        })
    }
    checkOut=()=>{
        let idList=[];
        let indexList=[];
        for(let i=0;i<this.state.checkOne.length;i++){
            if(this.state.checkOne[i]){
                indexList.push(i)
                idList.push(this.props.shopCar[i]._id)
            }
        }
        let total = this.state.total;
        idList.map((val,index)=>{
            this.deleteGood(val,indexList[index]);
        })
        this.setState({
            beforeTotal:total,
            modal2: false,
            modal1:true,
          });
    }
    render(){
        let isShow = {
            display:this.state.isShow
        }
        return(
            <div className="shopcar">
                <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => this.props.history.go(-1)}
                >购物车</NavBar>
                <ul className="shopcar-box">
                <div className="shopcar-tip" style={isShow}>
                    <img src={require("../../../assets/images/icon/nopro.jpg")} alt=""/>
                    <p>购物车空空如也</p>
                </div>
                {
                  this.props.shopCar.map((val,index)=>{
                           return(
                            <li key={index} ref={"box"+index} className="animated flipInX">
                                <div className="shopcar-left">
                                    <AgreeItem checked={this.state.checkOne[index]} onClick={()=>this.checkOne(index)}></AgreeItem>
                                </div>
                                <div className="shopcar-mid">
                                <img src={"http://s1.mayn.com.cn/ns"+val.img} alt=""/>   
                                    <div className="continer">
                                        <p>{val.name}</p>
                                        <span>￥{val.price}</span>
                                    </div>
                                </div>
                                <div className="shopcar-right">
                                        <Stepper
                                        style={{ width: '100%', minWidth: '100px' }}
                                        showNumber
                                        max={10}
                                        min={1}
                                        onChange={value=>{this.updateShopCar(val.mId,value)}}
                                        defaultValue={val.num}
                                        />
                                        <p onClick={()=>this.showAlert2(val._id,index)}><i className="iconfont icon-lajixiang"></i></p>
                                </div>
                            </li>
                           )
                    })
                } 
                </ul>
                <div className="checkOut">
                    <div className="shopcar-left">
                        <AgreeItem checked={this.state.checkAll} onClick={()=>{this.checkAll()}}></AgreeItem>
                    </div>
                    <div className="shopcar-mid">
                        <h1>实付尾款：￥{this.state.total}</h1>
                        <p>总额:￥{this.state.total}</p>
                    </div>
                    <div className="shopcar-right">
                            <button onClick={this.showModal('modal2')}>结算</button>
                    </div>
                </div>
                <Modal
                popup
                visible={this.state.modal2}
                onClose={this.onClose('modal2')}
                animationType="slide-up"
                >
                <List renderHeader={() => <div>您的账单</div>} className="popup-list">

                    <Item extra={sessionStorage.getItem("username")}>用户名</Item>
                    <Item extra={"￥"+this.state.total}>总额</Item>
                    <Item extra={"￥"+this.state.total}>实付金额(包含运费)</Item>
                    <List.Item>
                    <Button type="primary" onClick={this.checkOut}>买入</Button>
                    </List.Item>
                </List>
                </Modal>
                     <Modal
                    visible={this.state.modal1}
                    transparent
                    maskClosable={false}
                    onClose={this.onClose('modal1')}
                    title="Title"
                    footer={[{ text: 'Ok', onPress: () => { console.log('ok'); this.onClose('modal1')(); } }]}
                    wrapProps={{ onTouchStart: this.onWrapTouchStart }}
                    >
                    <div style={{ height: 100, overflow: 'scroll' }}>
                    <Result
                    className="pay"
                    img={myImg('https://gw.alipayobjects.com/zos/rmsportal/pdFARIqkrKEGVVEwotFe.svg')}
                    title="支付成功"
                    message={<div>{this.state.beforeTotal}元<del>{this.state.beforeTotal*(1.1).toFixed(2)}元</del></div>}/>
                    </div>
                    </Modal>
             
            </div>
        )
    }
    componentDidMount(){
        let timer=setTimeout(()=>{
            for(let i=0;i<=this.state.checkOne.length;i++){
                this.refs["box"+i].className="";
            }
            clearTimeout(timer);
        },500)
        
    }
}


