import React, {Component} from "react"
import {Switch,Route,HashRouter,NavLink,Redirect} from "react-router-dom"
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import Footer from "../footer"
import "./index.scss"
import Goods from "../goods"
import Mine from "../mine"
import Main from "../main"
import Type from "../type"
import ShopCar from "../shopCar"
export default class Home extends Component{
    render(){

        return(
            <div>
                <Switch>
                    {/* <Redirect path="/app/" to="/app/main"/> */}
                    {/* <Route  component={Main}/> */}
                    {/* <Route component={Main}/> */}
                    <Route path="/app/" exact render={({match,location,history})=>{
                        return <Redirect to="/app/home"/>
                    }} />
                    <Route path="/app/home/"  component={Main}/>
                    <Route path="/app/type/"  component={Type}/>
                    <Route path="/app/mine/"  component={Mine}/>
                    <Route path="/app/shopCar/"  component={ShopCar}/>
                </Switch>
            </div>
        )
    }
}


