import React, {Component} from "react"
import { Result, Icon, WhiteSpace } from 'antd-mobile';

const myImg = src => <img src={src} className="spe am-icon am-icon-md" alt="" />;

export default class Pay extends Component{
    render(){
        // console.log(this.props.match.params.total)
        return(
            <div>
                    <Result
                        img={myImg('https://gw.alipayobjects.com/zos/rmsportal/pdFARIqkrKEGVVEwotFe.svg')}
                        title="支付成功"
                        // message={<div>{this.props.match.params.total}元 <del>{this.props.match.params.total}元</del></div>}
                    />
            </div>
        )
    }
}


