import React, {Component} from "react"
import {Switch,Route,HashRouter,NavLink,Redirect} from "react-router-dom"

import Home from "./home"
import Goods from "./goods"
import Footer from "./footer"
import Detail from "./detail"
import Login from "./login"
import Pay from "./pay"


export default class App extends Component{
    render(){
        return(
            <div>
                <HashRouter basename="">
                <Switch>
                    <Route path="/" exact render={({match,location,history})=>{
                        return <Redirect to="/app/home"/>
                    }} />
                    {/* <Route component={Home}/> */}
                     <Route path="/app/" component={Home} strict/>
                     <Route path="/goods" component={Goods}/>
                     <Route path="/detail/:mId" component={Detail}/>
                    <Route path="/login" component={Login}/>
                    <Route path="/pay" component={Pay}/>

                     <Route path="/" exact render={({match,location,history})=>{
                        return <Redirect to="/app/:total"/>
                     }} /> 
                </Switch>

                </HashRouter>
                

            </div>

        )
    }
}


