
const defaultState = {
    HotGoods:[],
    NewGoods:[],
    Goods:[],
    shopCar:[],
    page:1,
    tip:"上拉加载更多...",
    Detail:{}
   
}
export default (state = defaultState,action)=>{
    switch(action.type){
        case "NewGoods":
        return {...state,...{NewGoods:action.data}};
        break;
        case "HotGoods":
        return{...state,...{HotGoods:action.data}};
        break;
        case "TypeGoods":
        if(action.data.length<1){
            return{...state,...{Goods:action.data},page:1,tip:"已无更多"};
        }else{
            return{...state,...{Goods:action.data},page:1,tip:"上拉加载更多..."};
        }
        break;

        case "ForMore":
        // return {...state,...{Goods:action.data},page:state.page*1+1}
        if(action.data.length<1){
            return{...state,page:state.page*1+1,tip:"已无更多 :)"};            
        }else{
            return{...state,...{Goods:[...state.Goods,...action.data]},page:state.page*1+1};
        }
        break;

        case "getDetail":
        return{...state,...{Detail:action.data}}
        break;

        case "getShopCar":
        return{...state,...{shopCar:action.data.result}}
        break;

        default:
        return state
        break;
    }
}


