import React, {Component} from "react"
import { SearchBar, Button, WhiteSpace, WingBlank,Carousel,Tabs,Grid} from 'antd-mobile';
import { StickyContainer, Sticky } from 'react-sticky';
import axios from "axios"
import "./index.scss";
import {NavLink} from "react-router-dom"

import {connect} from "react-redux";
import {HotGoods,TypeGoods,NewGoods} from "../../actions"
import Footer from "../footer"
@connect(
    state=>{
            return{HotGoods:state.HotGoods,
                    NewGoods:state.NewGoods}
    }
)

export default class Main extends Component{


    componentWillMount(){
        this.props.dispatch(HotGoods())
        this.props.dispatch(NewGoods())
    }


    render(){
        console.log(this.props)
        let  data=[
            require("../../../assets/images/banner1.jpg"),
            require("../../../assets/images/banner2.jpg"),
            require("../../../assets/images/banner3.jpg"),
            require("../../../assets/images/banner4.jpg"),
           ]

        let icon=[
            {name:"商城",img:require('../../../assets/images/icon/main1.png')},
            {name:"采购",img:require('../../../assets/images/icon/main2.png')},
            {name:"物流",img:require('../../../assets/images/icon/main3.png')},
            {name:"介绍",img:require('../../../assets/images/icon/main4.png')},
            {name:"交流",img:require('../../../assets/images/icon/main5.png')},
            {name:"动态",img:require('../../../assets/images/icon/main6.png')},
            {name:"联系",img:require('../../../assets/images/icon/main7.png')},
            {name:"更多",img:require('../../../assets/images/icon/main8.png')},
        ]
        let hot =[]
        this.props.HotGoods.map((val)=>{
            hot.push({icon:"http://s1.mayn.com.cn/ns"+val.mImg})
        })
        const tabs = [
            { title: '月最热' },
            { title: '周最热' },
            { title: '日最热' },
          ];
        
        const adv = [
            {p:"RTX2080来袭，你准备好了吗"},
            {p:"新年折扣，满5000-500"},
            {p:"欢庆双12，折扣券随便发"},
        ]

          
        
        const title = require('../../../assets/images/icon/title.jpg');
        return(

            <div className="main-body">
                <SearchBar onSubmit={value =>{this.props.history.replace(`/goods?key=${value}`);this.props.dispatch(TypeGoods({key:value}))}}  placeholder="搜你想要的" maxLength={8} style={{width:"70%"}} />
                <Carousel
                autoplay={true}
                infinite
                className="banner"
                >
                {data.map((val,index) => (
                    <img
                    key={index}
                    src={val}
                    alt=""
                    style={{ width: '100%', verticalAlign: 'top',height:'100%'}}
                    />
                ))}
                </Carousel>
                <div className="nav-box">
                    <ul className="nav-meanu">
                    {
                        icon.map((val,index)=>{
                            return(
                                <li key={index}>
                                    <i className="icon">
                                        <img src={val.img} alt=""/>
                                    </i>
                                    <span>
                                        {val.name}
                                    </span>
                                </li>
                            )
                        })  
                    }  
                    </ul>
                </div>
                <WhiteSpace/>
                <WingBlank>
                <div className="roll-p">
                    <img src={title}/>
                    <Carousel className="my-carousel rollAdv"
                    vertical
                    dots={false}
                    dragging={false}
                    swiping={false}
                    autoplay
                    infinite>
                       {
                           adv.map((val,index)=>{
                               return(
                                   <div key={index} className="v-item"><span style={{float:"left",color:"red",fontWeight:"bold"}}>HOT</span><p style={{marginLeft:50}}>{val.p}</p></div>
                               )
                           })
                       }
                    </Carousel>
                </div>
                </WingBlank>
                <WhiteSpace/>
                <WingBlank>
                <div className="miaosha">
                      <h3>热卖推荐</h3>
                       <ul>
                          {
                              this.props.HotGoods.map((val,index)=>{
                                  return(
                                    <li key={index} style={{textAlign:"center"}}>
                                       <NavLink to={"/detail/"+val.mId}>
                                        <img src={"http://s1.mayn.com.cn/ns"+val.mImg} alt=""/>
                                        </NavLink>
                                        <del>￥{val.costPrice}</del>
                                        <p>￥{val.discountPrice}</p>
                                    </li>
                                  )
                              })
                          }
                        </ul>
                </div>
                <WhiteSpace/>
                <div className="miaosha">
                      <h3>新品预约</h3>
                       <ul>
                          {
                              this.props.NewGoods.map((val,index)=>{
                                  return(
                                    <li key={index} style={{textAlign:"center"}}>
                                        <NavLink to={"/detail/"+val.mId}>
                                        <img src={"http://s1.mayn.com.cn/ns"+val.mImg} alt=""/>
                                        </NavLink>
                                        <del>￥{val.costPrice}</del>
                                        <p>￥{val.discountPrice}</p>
                                    </li>
                                  )
                              })
                          }
                        </ul>
                </div>
                </WingBlank>
                <Footer/>

                
            </div>
        )
    }
}


