
import axios from "axios"
axios.defaults.baseURL = "http://39.107.100.105:8000";
export const HotGoods = () => (
    axios.get("/pc/getGoods",{params:{sort:"mCount",limit:6}})
    .then(res=>(
        {
            type:"HotGoods",
            data:res.data
        }
    ))
)
export const NewGoods = (params2) =>(
    axios.get("/pc/getGoods",{params:{sort:"mCount",isSort:1,limit:6}}).then((res)=>(
        {
            type:"NewGoods",
            data:res.data
        }
    ))
)
export const TypeGoods = (params2) =>(
    axios.get("/pc/getGoods",{params:params2}).then((res)=>(
        {
            type:"TypeGoods",
            data:res.data
        }
    ))
)

export const ForMore = (params) =>(
    axios.get("/pc/getGoods",{params:params}).then((res)=>(
        {
            type:"ForMore",
            data:res.data
        }
    ))
)
export const getDetail = (params) =>(
    axios.get("/pc/getDetail",{params:params}).then((res)=>(
        {
            type:"getDetail",
            data:res.data[0]
        }
    ))
)
export const getShopCar = (params) =>(
    axios.get("/pc/getShopCar",{params:params}).then((res)=>{
        return{
            type:"getShopCar",
            data:res.data
        }
    })
)
