import React, {Component} from "react"
import {NavLink} from "react-router-dom"
import { Toast,NavBar, Icon,WhiteSpace, WingBlank,SearchBar,PullToRefresh} from 'antd-mobile';
import {connect} from "react-redux";
import {TypeGoods,ForMore} from "../../actions"
import "./index.scss"
@connect(
    state=>{
            return{...state}
    }
)

export default class Goods extends Component{

    getMore=()=>{
        let search = new URLSearchParams(this.props.location.search).get("goodTypeId");
        let key = new URLSearchParams(this.props.location.search).get("key")
        let {dispatch,page} = this.props;
        page++
        dispatch(ForMore({goodTypeId:search,page:page,key}))
    }

    componentWillMount(){

        Toast.loading('疯狂加载中...', 1, () => {
            let search = new URLSearchParams(this.props.location.search).get("goodTypeId");
            let page = new URLSearchParams(this.props.location.search).get("page")
            let key = new URLSearchParams(this.props.location.search).get("key")
            this.props.dispatch(TypeGoods({goodTypeId:search,page:page,key:key}))
        });      
    }   

    render(){
        console.log(this.props)
      
        return(
            <div className="goods">
                <div className="top-box">
                    <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.go(-1)}
                    >商城</NavBar>
                    <SearchBar
                        className="search-box"
                        placeholder="Search"
                        onSubmit={value =>{this.props.history.replace(`/goods?key=${value}`);this.props.dispatch(TypeGoods({key:value}))}}
                        onCancel={value =>{this.props.history.replace(`/goods?key=${value}`);this.props.dispatch(TypeGoods({key:value}))}}
                        // onClear={value => console.log(value, 'onClear')}
                        // onCancel={() => console.log('onCancel')}
                        showCancelButton
                        // value={new URLSearchParams(this.props.location.search).get("key")}
                        // onChange={(value)=>}
                        cancelText={"搜索"}
                    />
                </div>
                <PullToRefresh
                    damping={100}
                    distanceToRefresh={30}
                    ref={el => this.ptr = el}
                    style={{
                    height: "100vh",
                    overflow: 'auto',
                    }}
                    // indicator={{ deactivate: '上拉可以刷新' }}
                    direction={'up'}
                    // refreshing={this.state.refreshing}
                    onRefresh={() => {
                    this.setState({ refreshing: true });
                    setTimeout(() => {
                        this.getMore()
                        this.setState({ refreshing: false });
                    }, 1000);
                    }}
                >
                <div className="goods-List">
                {
                    this.props.Goods.map((val,index)=>{
                        const delay = {
                            animationDelay:index*100+'ms',
                        }
                        return(
                            <li key={index} >
                            <NavLink to={"/detail/"+val.mId}>
                                <div className="item-pic">
                                <img src={"http://s1.mayn.com.cn/ns"+val.mImg} alt=""/>
                                </div>
                                <div className="item-right">
                                    <p className="item-title">{val.mName}</p>
                                    <p className="item-intro">{val.slogan}</p>
                                    <p className="item-price">￥{val.discountPrice}</p>
                                </div>
                            </NavLink>
                            </li>
                        )
                    })
                }
                <span>{this.props.tip}[<a onClick={()=>{this.getMore()}}>点击刷新</a>]</span>
                </div>
                </PullToRefresh>

            </div>
        )
    }
}


