import React, {Component} from "react"
import { Tabs, WhiteSpace,NavBar,Icon } from 'antd-mobile';
import {Route,NavLink,Redirect} from "react-router-dom"

import "./index.scss"
export default class Type extends Component{
    onValueChange(value){

        console.log(value)
        switch(value.title){
            case "游戏主机":
            console.log("1");
            break;
            case "家用主机":
            console.log("2");
            break;
            case "办公主机":
            console.log("3");
            break;
            default:
            console.log("4");
            break;
        }
    }
    render(){
        const tabs = [
            { title: '游戏主机' },
            { title: '办公主机' },
            { title: '家用主机' },
            { title: '主机配件' },
            { title: '其他' },
          ];
        const nav=[
            {img:require("../../../assets/images/icon/gamePc.jpg"),title:"游戏主机",path:"/goods?goodTypeId=1"},
            {img:require("../../../assets/images/icon/homePc.jpg"),title:"办公主机",path:"/goods?goodTypeId=25"},
            {img:require("../../../assets/images/icon/officePc.jpg"),title:"家用主机",path:"/goods?goodTypeId=24"},
        ] 
        return(
            <div>
                <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => this.props.history.go(-1)}
                >购物车</NavBar>
                 <div style={{ height:'91.5vh' }}>
                    <Tabs tabs={tabs}
                    initalPage={'t2'}
                    tabBarPosition="left"
                    tabDirection="vertical"
                    onChange={this.onValueChange}
                    >
                    {
                        nav.map((val,index)=>{
                            return(
                                <div key={index} className="pc-type">
                                    <li>
                                        <NavLink to={val.path}>
                                        <img src={val.img} alt=""/>
                                        </NavLink>
                                        <span>{val.title}</span>
                                    </li>
                                </div>
                            )
                        })
                    }
                    
                  
                    </Tabs>
                    <WhiteSpace />
                </div>
            </div>
        )
    }
}



