import React, {Component} from "react";

import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import {NavLink} from "react-router-dom"

import "./index.scss"


export default class Foot extends Component{

    state = {
        foots:[
            {txt:"首页",path:"/app/home/",icon:"icon-zhuye"},
            {txt:"商品",path:"/app/type/",icon:"icon-shangpin"},
            {txt:"添加",path:"/app/shopCar/",icon:"icon-gouwuche"},
            {txt:"我",path:"/app/mine/",icon:"icon-wo"},
        ]
    }

    // componentWillMount(){
    //     let {location} = this.props;
    //     var pathname = location.pathname.replace("/app/",'');
    //     console.log(pathname);

    //     this.setState({
    //         selectedTab:pathname+"Tab"
    //     })
    // }

    render(){
        return (
            <footer>
                {
                    this.state.foots.map((foot,index)=>{
                        return (
                            <div key={index}>
                                <NavLink to={foot.path}  activeClassName="nav-active">
                                    <i className={'iconfont '+foot.icon}></i>
                                </NavLink>
                            </div>
                        )
                    })
                }
            </footer>
        )
    }
}


